/*Edit Decks*/

.App {
  max-width: 800px;
}

 @font-face {
  font-family: 'PhosphatePro-Solid';
  src: local('PhosphatePro-Solid'), url(./Fonts/PhosphatePro-Solid.otf) format('opentype');
}

body{
  height: 100vh;
  box-sizing: border-box;
}

* {
  font-size: 100%;
  font-family: PhosphatePro-Solid;
 }

.BlockContainer{
  display: flex;
  justify-content: space-between;
}

.TableContainer{
  display: flex;
  justify-content: space-between;

}

.TableContainer > table{
  width: 100%;
  table-layout: fixed;
  
}
.TableContainer > td{
  height: 1px;
  width: 50%;
  padding: 0;
}


.FillContainer{
  display: flex;
  justify-content: stretch;
  
}
 .FillContainer > *{
  flex-grow: 1;
}


.ButtonIcon{
  max-width: 50px;
}
.Right{
  display: inline-flex;
  float: right;
}

.column ul{
  padding: 0;
  margin: 0.5rem;
}

.column {
  display: flex;
  flex-direction: column;

  text-align: center;
  width: 50%;
}

.center{
  text-align: center;
}

/* Clear floats after the columns */
.row {
  display: flex;
}

.flip{
  transform: scaleX(-1)
}

.CardsList{
  margin: 0 auto;
  
  overflow-y: auto;
}

.Card{
background-color: greenyellow;
border-width: 1px;
border-style: solid;
border-color: black;
height: 4vh;
min-height: 25px;
max-height: 50px; 
line-height: 4vh;
width: 95%;
}

.containerEditDecks {
  background-color: #1c1e21;
  color: rgb(199, 199, 199);
  display: grid;
  height: 100vh;
  grid-template-rows: 50px 50px minmax(0%, 100%) 75px ;
}

.EvenMargin{
  width: 50%;
  margin: 2px;
}

.Selected{
  background-color: #FBCE5D;
}

